@font-face {
  font-family: 'Familiada Regular';
  src: url(/Familiada-React/static/media/familiada.401a7905.ttf) format('truetype'),
    url(/Familiada-React/static/media/familiada.bed6fdce.woff) format('woff'),
    url(/Familiada-React/static/media/familiada.b895ff28.svg) format('svg'),
    url(/Familiada-React/static/media/familiada.2e6dc538.eot) format('eot');
  font-weight: normal;
  font-style: normal;
}

* {
  margin: 0;
  padding: 0;
  box-sizing: border-box;
  background-color: #000;
  color: yellow;
}

body {
  display: -webkit-flex;
  display: flex;
  -webkit-align-items: center;
          align-items: center;
  -webkit-justify-content: center;
          justify-content: center;
  height: 100vh;
  width: 100vw;
  font-family: 'Familiada Regular';
  text-transform: uppercase;
  font-size: 10px;
}
.btn {
  border: 1px dotted yellow;
  font-size: 2em;
  font-family: 'Familiada Regular';
  padding: 2vh 4vw;
  margin: 2vw;
}
input {
  border: 1px dotted yellow;
  colort: yellow;
  padding: 5px;
  font-size: 0.7em;
}
@media (orientation: landscape) and (min-width: 1024px) {
  .btn {
    border-width: 3px;
    font-size: 4em;
  }
  input {
    border-width: 3px;
    padding: 10px;
  }
}
@media (orientation: landscape) and (min-width: 1366px) {
  .btn {
    border-width: 4px;
    font-size: 5em;
  }
  input {
    border-width: 4px;
    padding: 10px;
  }
}

.sideBoard, .fails, .poinst{
    display: -webkit-flex;
    display: flex;
    -webkit-justify-content: space-around;
            justify-content: space-around;
    -webkit-align-content: center;
            align-content: center;
    text-align: center;
}

.sideBoard{
    width: 10%;
    height: 100%;
    -webkit-flex-direction: column;
            flex-direction: column;
}
.fail{
    padding: 3vh 0;
}
.fails{
  display: -webkit-flex;
  display: flex;
  -webkit-flex-direction: column;
          flex-direction: column;
  height: 80%;
  -webkit-align-items: center;
          align-items: center;
  -webkit-justify-content: center;
          justify-content: center;
  font-size: 3em;
}
.points{
    display: -webkit-flex;
    display: flex;
    -webkit-align-items: center;
            align-items: center;
    -webkit-justify-content: flex-end;
            justify-content: flex-end;
    padding: 2vw;
    min-height: 20vh;
    width: 100%;
    font-size: 2em;

}
@media(orientation:landscape) and (min-width: 1024px){

    .fails{
      font-size: 5em;
    }
    .points{

        padding: 2vw;
        min-height: 20vh;
        width: 100%;
        font-size: 5em;
    }
}


.mainBoard
{
    width: 80%;
    height: 100%;
    display: -webkit-flex;
    display: flex;
    -webkit-align-items: center;
            align-items: center;
    -webkit-justify-content: center;
            justify-content: center;
}
.startingPage{
    display: -webkit-flex;
    display: flex;
    -webkit-flex-direction: column;
            flex-direction: column;
    -webkit-align-items: center;
            align-items: center;
    -webkit-justify-content: space-around;
            justify-content: space-around;
    width: 100%;
    height: 100%;
}
.familiada{
    font-size: 3em;
}
.startingPage label{
    font-size: 1.5em;
    display: -webkit-flex;
    display: flex;
    -webkit-align-items: center;
            align-items: center;
}
@media(orientation: landscape) and (min-width: 1024px ){
    .familiada{
        font-size: 7em;
    }
    .startingPage label{
        font-size: 2.5em;
    }
}
@media(orientation: landscape) and (min-width: 1366px ){
    .familiada{
        font-size: 9em;
    }
    .startingPage label{
        font-size: 3em;
    }
}
.questionBox{
    display: -webkit-flex;
    display: flex;
    -webkit-flex-direction: column;
            flex-direction: column;
    -webkit-align-items: center;
            align-items: center;
    -webkit-justify-content: space-between;
            justify-content: space-between;
    height: 100%;
    width: 100%;
}
.winner{
    display: -webkit-flex;
    display: flex;
    -webkit-align-items: center;
            align-items: center;
    -webkit-justify-content:center;
            justify-content:center;
    font-size: 3em;
}
.question{
    font-size: 2em;
    line-height: 2.2em;
    padding: 4vw 0;
    width:80%;
}
.answers{
    width: 80%;
    display: -webkit-flex;
    display: flex;
    -webkit-flex-direction: column;
            flex-direction: column;
    -webkit-align-items: center;
            align-items: center;
    -webkit-justify-content: space-around;
            justify-content: space-around;
}
.answerBox{
    width: 100%;
    display: -webkit-flex;
    display: flex;
    -webkit-align-items: center;
            align-items: center;
    -webkit-justify-content: space-between;
            justify-content: space-between;
    padding: 2vh;
    font-size: 1.5em;
    line-height: 1.6em;
}
.answerForm{
    justify-self: flex-end;
    position: relative;
}
.answerForm label{
    font-size: 1.3em;
    padding: 2vw;
}
.answerForm input{
    margin: 10px 5px;
}
.answerFormBtn{
    border: 1px dotted yellow;
    font-family: 'Familiada Regular';
    padding: 2vh 2vw;
    margin: 2vw;
}
.activeTeam{
    font-size: 1.3em;
}
@media(orientation: landscape){
    .winner{
        font-size: 2em;
    }
    .question{
        font-size: 1.5em;
        line-height: 1.6em;
    }
    .answerBox{
        padding: 2vh;
        font-size: 1.2em;
        line-height: 1.3em;
    }
    .answerForm{
        justify-self: flex-end;
        -webkit-align-self: center;
                align-self: center;
        width: 80%;
    }
    .answerForm label{
        font-size: 1.1em;
        padding: 2vw;
    }
    .answerForm input{
        padding: 2px;
    }
    .formBtn{
        padding: 2vh 2vw;
        margin: 2vh;
    }
}
@media(orientation: landscape) and (min-width: 731px){
    .winner{
        font-size: 3em;
    }
    .question{
        font-size: 2em;
        line-height: 2.1em;
    }
    .answerBox{
        padding: 2vh;
        font-size: 1.5em;
        line-height: 1.6em;
    }
    .answerForm label{
        font-size: 1.3em;
        padding: 2vw;
    }
    .answerForm input{
        padding: 5px;
    }
    .formBtn{
        padding: 2vh 2vw;
        margin: 2vh;
    }
}
@media(orientation: landscape) and (min-width: 1024px){
    .winner{
        font-size: 5em;
    }
    .question{
        font-size: 3.5em;
    }
    .answerBox{
        font-size: 2.8em;
    }
    .answerForm label{
        font-size:2em;
    }
    .answerForm input{
        padding: 5px;
        font-size: 1.5em;
    }
    .formBtn{
        font-size: 1.5em;
    }
}
@media(orientation: landscape) and (min-width: 1366px){
    .activeTeam{
        font-size: 2em;
        position: absolute;
        top: 0;
        left: 0;
        margin-left: 3%;
    }
}
.app{
    display: -webkit-flex;
    display: flex;
    -webkit-align-items: center;
            align-items: center;
    -webkit-justify-content: center;
            justify-content: center;
    width: 100vw;
    height: 100vh;
    max-height: 100vh;
    max-width: 1440px;
}
.turnHorizon{
    position: fixed;
    z-index: 10;
    top: 0;
    left: 0;
    height: 100vh;
    width: 100vw;
    display: -webkit-flex;
    display: flex;
    -webkit-align-items: center;
            align-items: center;
    -webkit-justify-content: center;
            justify-content: center;
    font-family: 'Montserat', sans-serif;
    font-size: 2em;
}
.turnHorizon h2{
    width: 80%;
    text-align: center;
}
@media(orientation: landscape){
    .turnHorizon{
       display: none;
    }
}
.whosFirst{
